<script>


	import {officialList,queryCarousels} from '../../api/info';
	export default {
		data() {
			return {
				 dialogVisible:true,
				dataList:[],
				YearMonth:'2018',
				oilType:'',
				total:0,
				pageNum:0,
				pageSize:0,
				imgUrlBanner:'',
			}
		},
		methods: {
//			getYear(){
//				
//				this.YearMonth = new Date().getFullYear().toString()
//			},
		    getData:function(){
		    	var param = {
		    		'crudeNameE':'',
		    		'startDate':this.YearMonth,
		    		'pageNum' :this.pageNum,
		    		'pageSize': this.pageSize
		    	}
		        officialList(param).then((res)=>{
//		        	console.log(res)
						this.dataList=res.datas;
						this.total = res.total
				})
		    },
		    trendFn(){
		    	this.$router.push({path:'/officialRate'})
		    },
		    pageFn(sty){
		    	this.pageNum = sty
		    	var param = {
		    		'crudeNameE':this.oilType,
		    		'startDate':this.YearMonth,
		    		'pageNum' :sty,
		    		'pageSize': this.pageSize
		    	}
		        officialList(param).then((res)=>{
//		        	console.log(res)
						this.dataList=res.datas;
						this.total = res.total
				})
		    },
		    searchFn(){
//		    	console.log(this.YearMonth)
		    	if(this.YearMonth == null){
		    		this.$message({
			          message: '请选择年份',
			          type: 'warning'
			        });
		    		return
		    	}
		    	var param = {
		    		'crudeNameE':this.oilType,
		    		'startDate':this.YearMonth,
		    		'pageNum' :this.pageNum,
		    		'pageSize': this.pageSize
		    	}
		        officialList(param).then((res)=>{
						this.dataList=res.datas;
						this.total = res.total
				})
		    },
		    getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			},
			show1(){
				var param = {
		    		'crudeNameE':this.oilType,
		    		'startDate':this.YearMonth,
		    		'pageNum' :this.pageNum,
		    		'pageSize': this.pageSize
		    	}
		        officialList(param).then((res)=>{
		        	console.log(res)
						this.dataList=res.datas;
						this.total = res.total
				})
			}
		},
//		watch:{
//			oilType:function(val){
//				var param = {
//		    		'crudeNameE':val,
//		    		'startDate':this.YearMonth,
//		    		'pageNum' :this.pageNum,
//		    		'pageSize': this.pageSize
//		    	}
//		        officialList(param).then((res)=>{
//		        	console.log(res)
//						this.dataList=res.datas;
//						this.total = res.total
//				})
//			}
//		},
		mounted(){
//			this.getYear()
			this.getData();
			this.getBanner();
			
    	}
	}

</script>
